.navbar {
    padding: 0;
    margin: 0;
    height: 4rem;
    width: 100vw;
    font-size: 1.5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    z-index: 100;
    position: fixed;
    top: 0;
    background-color: rgb(255, 255, 255);
  }
  
  .nav-link {
    color: rgba(13, 126, 196, 0.5);
    font-family: 'Cutive Mono', monospace;
  }
  
  .nav-link:hover {
    color: rgba(13, 126, 196, 0.9);;
    background-color: linen;
    transition: ease-in-out 0.2s all;
  }
.recipe__card {
    width: 100%;
    height: 100%;
    border: 2px solid rgba(48, 48, 48, 0.735);
    cursor: pointer;
  }
  
  .recipe__card:hover {
    /* border: 2px solid rgba(13, 126, 196, 0.625); */
    box-shadow: 15px 10px 0px 0px rgba(13, 126, 196, 0.413);
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
    transition: ease-in-out 0.2s all;
  }
  
  .recipe__card:hover .recipe__card__tag__item {
    background-color: thistle;
    text-decoration: none;
  }
  .recipe__card:hover .recipe__card__name {
    text-decoration: underline;
    -webkit-text-decoration-color: rgba(13, 126, 196, 0.625);
            text-decoration-color: rgba(13, 126, 196, 0.625);
  }
  
  .recipe__card__content {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-text-decoration-color: rgba(13, 126, 196, 0.625);
            text-decoration-color: rgba(13, 126, 196, 0.625);
  }
  
  .recipe__card__img {
    width: 100%;
  }
  
  .recipe__card__name {
    font-size: 2rem;
    color: rgb(50, 53, 58);
    text-align: center;
  }
  
  .recipe__card__tag {
    width: 90%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    text-decoration: none;
    padding: 0.5rem;
    color: grey;
  }
.search-bar {
    padding: 1rem;
    position: relative;
  }
  
  .search__actions {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .search__form {
    display: -webkit-flex;
    display: flex;
  }
  
  .search__input {
    padding: 0.59rem;
    border: 2px solid rgb(55, 152, 213);
  }
  
  .search__button {
    padding: 0.48rem;
    border: 2px solid rgba(13, 126, 196, 0.625);
    background-color: rgb(108, 183, 230);
    font-family: 'Roboto Mono', monospace;
    cursor: pointer;
  }
  
  .search__button:hover {
    color: white;
    transition: ease-in-out 0.2s all;
  }
.filter__button-add {
    margin-left: 0.5rem;
    padding: 0.5rem;
    cursor: pointer;
    background-color: rgba(199, 199, 199, 0.714);
    font-family: 'Roboto Mono', monospace;
    color: black;
  }
  
  .filter__button-add:hover {
    background-color: thistle;
    color: white;
    transition: ease-in-out 0.2s all;
  }
  
  .filter-button {
    margin: 0.2rem;
    padding: 0.3rem;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.495);
  }
  
  .filter-button:hover {
    transition: ease-in-out 0.2s all;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    color: linen;
    background-color: rgba(86, 165, 215, 0.79);
  }
  
  .filter-button.active {
    background-color: rgba(86, 165, 215, 0.79);
  }
  
  .filters {
    width: 100%;
    height: 100%;
    display: block;
    position: fixed;
    /* Stay in place */
    z-index: 200;
    /* Sit on top */
    padding-top: 3rem;
    /* Location of the box */
    left: 0;
    top: 0;
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4);
    /* Black w/ opacity */
  }
  
  .filters__content {
    background-color: thistle;
    font-size: 1rem;
    margin: auto;
    padding: 1rem;
    width: 300px;
    height: 520px;
    line-height: 1.1;
    position: relative;
  }
  
  .filters__group {
    margin-bottom: 1rem;
  }
  .filters-selected {
    position: absolute;
    top: 4rem;
    width: 800px;
  }
  .filters__item-selected {
    background-color: thistle;
    margin-right: 0.2rem;
    padding: 0.2rem;
  }
  
  .close__button {
    position: absolute;
    top: 1rem;
    right: 1.2rem;
    cursor: pointer;
    background-color: transparent;
    font-size: 1.5rem;
  }
  
  .close__button:hover {
    color: rgba(13, 126, 196, 0.625);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: ease-in-out 0.2s all;
  }

  @media screen and (min-width: 600px) {
    .filters {
      padding-top: 5rem;
    }
    .filters__content {
      width: 500px;
      height: 500px;
      padding: 3rem 2rem;
    }

    .filters__tag {
      font-size: 1.5rem;
    }
    
  }
